  export default {

    data() {
      return {
		  elements: [{
		  		title: '打卡',
		  		name: 'Clockin',
		  		color: 'cyan',
		  		cuIcon: 'location',
				url:'/pages/user/clockin/index'
		  	},
		  	{
		  		title: '赋分',
		  		name: 'Score',
		  		color: 'blue',
		  		cuIcon: 'appreciate',
				url:'/pages/getscore/menu'
		  	},
		  	{
		  		title: '赋分记录',
		  		name: ' Record ',
		  		color: 'olive',
		  		cuIcon: 'copy',
				url:'/pages/getscore/scoreRecord'
		  	},
		  	{
		  		title: '志愿者打卡',
		  		name: 'Information',
		  		color: 'green',
		  		cuIcon: 'locationfill',
				url:'/pages/user/clockin/volunteer'
		  	}
		  ],
		  swiperList: [{
		  	id: 0,
		  	type: 'image',
		  	url: '/static/image/垃圾分类系统头部.png'
		  },
		  {
		  	id: 1,
		  	type: 'image',
		  	url: '/static/image/垃圾分类系统头部.png'
		  }],
		  dotStyle: false,
	  };
    },
    mounted() {
		
		this.TowerSwiper('swiperList');
		// 初始化towerSwiper 传已有的数组名即可
	},
	methods:{
		// 初始化towerSwiper
		TowerSwiper(name) {
			let list = this[name];
			for (let i = 0; i < list.length; i++) {
				list[i].zIndex = parseInt(list.length / 2) + 1 - Math.abs(i - parseInt(list.length / 2))
				list[i].mLeft = i - parseInt(list.length / 2)
			}
			this.swiperList = list
		},
	}
  }
